

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    background-color: aliceblue;
  }
  
  .main_div{
    height: 100vh;
    width: 100%;
    background-color: #6983aa;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .main_div .center_div{
    height: 35rem;
    width:  22rem;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    align-items: center;
    margin: auto;
    padding-top: 2rem;
    border-radius: 1.5rem;
  }
  .main_div .center_div h2 {
    background-color: #9370db;
    color: #fff;
    width: 22rem;
    text-align: center;
    padding: .5rem 0rem;
    letter-spacing: .1rem;
    word-spacing: .2rem;
    font-size: 1.5rem;
    line-height: 1rem;  
}
  .main_div .center_div div{
      margin-top: 2rem;
      background-color: #fff
    }  ;
  
  .main_div .center_div div input{
    outline: none;
    border: none;
    color: #8566aa;
    background-color: #fff;
    font-size: 1.2rem;
    text-align: center;
    border-bottom: .1rem solid #8566aa;
    font-weight: 700;
    position: relative;
    left: 1.9rem;
    bottom:.2rem;
  }
  
  .main_div .center_div div button{ 
    font-size: 2rem;
    padding: 0rem 1rem;
    outline: none;
    border: none;
    background: #fff;
    border-color: transparent;
   background-color: #8566aa;
   position: relative;
   left: 30px;
   color: #fff;
   border-radius: 50%;
   min-height: 40px;
   box-shadow: 5px 5px 15px -5px rgba(0 ,0 ,0, 0.3);
   cursor: pointer;
   text-align: center;
  }
 .main_div .center_div div button:hover{
   background-color: #20bf6b;
 }
 ol {
   margin-top: 30px;
 }
 ol li{
   padding-left: 0px;
   text-align: left;
   font-size: 20px;
   font-weight: 500;
   min-height: 40px;
   display:flex;
   align-items: center;
   text-transform: capitalize;
 }
 .tode_style{
   display: flex;
   justify-content: left;
   align-items: center;
   flex-direction: row;
   position: relative;
   right: 7.8rem;
   background-color: #fff
 }
 .todo_style .fa-times {
   width: 20px;
   height: 20px;
   display: flex; 
   flex-direction: row;
   align-items: center;
   background-color: #9370db;
   border-radius: 50%;
   margin: 0 15px 0 35px;
   color: #fff;
   box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
   cursor: pointer;
   text-align: center;
 }
 .tode_style .fa-times:hover{
   background-color: crimson;
 }